import React, { Component } from "react";
import { MdInfo, MdLocationOn } from "react-icons/md";
import { Redirect } from "react-router-dom";
import { applicationPeriodsStoreInterface } from "../../../actions/application_periods";
import {
  candidateExamStoreInterface,
  InitialExamDetails,
} from "../../../actions/candidate_exam";
import { LocationsDataStoreInterface } from "../../../actions/locations_data";
import Loading from "../../../components/Loading/Loading";
import { LoadingType } from "../../../components/Loading/LoadingV2";
import { applicationExamAPI } from "../../../interfaces/application";
import { DATE } from "../../../utils/functions";
import { LocationTableInterface } from "../../Application/ExamCenter/ChooseExamCenter";

interface ExamDetailsProps {
  locationDetails: applicationExamAPI | undefined;
  examDetails: InitialExamDetails | null;
  locationsData: LocationsDataStoreInterface;
  loadAllLocationsData: (callback: (loading: boolean) => void) => void;
  setSelectedLocationData: (locationData: LocationTableInterface) => void;
  periods: applicationPeriodsStoreInterface;
  loadApplicationPeriods: (callback: (loading: boolean) => void) => void;
  candidateExam: candidateExamStoreInterface;
  setSelectedCandidateExam: (exam_id: string) => void;
  // selectedCourse: course;
}
interface ExamDetailsState {
  loading: boolean;
}

class ExamDetails extends Component<ExamDetailsProps, ExamDetailsState> {
  constructor(props: ExamDetailsProps) {
    super(props);

    this.state = {
      loading: false,
    };
  }
  setLoading = (status: boolean) => {
    this.setState({ loading: status });
  };
  componentDidMount = () => {
    if (
      this.props.locationsData.Locations_list === null ||
      this.props.locationsData.Locations_list.length === 0
    ) {
      this.props.loadAllLocationsData(this.setLoading);
    }
    // if (this.props.periods.applicationPeriods === null || this.props.periods.applicationPeriods.length === 0 || this.props.periods.selected_period === null) {
    this.props.loadApplicationPeriods(this.setLoading);
    // }
  };
  render() {
    if (this.props.candidateExam.selected_exam_id !== null) {
      return <Redirect to="/do-exam" />;
    }
    if (this.props.examDetails === null) {
      return (
        <div className="bg-yellow-50 text-yellow-800 font-bold rounded px-2 py-4 text-center text-3xl border border-yellow-600 mx-4 my-4">
          No Exam details found!
        </div>
      );
    }
    if (this.props.locationDetails === undefined) {
      return (
        <div className="bg-yellow-50 text-yellow-800 font-bold rounded px-2 py-4 text-center text-3xl border border-yellow-600 mx-4 my-4">
          No exam location details found!
        </div>
      );
    }
    return (
      <div>
        {this.state.loading === true ? (
          <div className="px-4 py-4 rounded bg-gray-100">
            <Loading type={LoadingType.dark} />
          </div>
        ) : (
          <div>
            <div className="bg-gray-100 rounded mx-2 mb-2 px-2 py-2">
              <div className="font-bold text-xl text-blue-600 mb-4 flex flex-row items-center justify-between gap-2 bg-white py-2 px-2 rounded">
                <div className="flex flex-row items-center gap-2">
                  <MdInfo className="text-4xl text-blue-600" />
                  Exam information
                </div>
                <button
                  onClick={() => {
                    // this.props.setSelectedCourse(this.props.selectedCourse);
                    this.props.examDetails &&
                      this.props.setSelectedCandidateExam(
                        this.props.examDetails.id
                      );
                  }}
                  type="button"
                  className={`btn ${
                    this.props.candidateExam.selected_exam_id !== null
                      ? "bg-yellow-700 hover:bg-yellow-900"
                      : "bg-blue-600 hover:bg-blue-700"
                  } text-white cursor-pointer text-base`}
                >
                  {this.props.candidateExam.selected_exam_id !== null
                    ? "Return to Exam"
                    : "Start Exam"}
                </button>
              </div>
              <div className="grid md:grid-cols-3 gap-4">
                <div className="col-span-1">
                  <div className="flex flex-col pl-2 border-gray-300 pb-2">
                    <span className="text-gray-500 text-sm italic">
                      Exam date:
                    </span>
                    <span className="text-gray-700 font-bold">
                      {DATE(this.props.examDetails.exam_date, "DD/MM/YYYY")}
                    </span>
                  </div>
                </div>
                <div className="col-span-1">
                  <div className="flex flex-col pl-2 border-gray-300 pb-2">
                    <span className="text-gray-500 text-sm italic">
                      Exam start time:
                    </span>
                    <span className="text-gray-700 font-bold">
                      {this.props.examDetails.start_time}
                    </span>
                  </div>
                </div>
                <div className="col-span-1">
                  <div className="flex flex-col pl-2 border-gray-300 pb-2">
                    <span className="text-gray-500 text-sm italic">
                      Exam duration:
                    </span>
                    <span className="text-gray-700 font-bold">
                      {this.props.examDetails.duration / 60} hours{" "}
                      {this.props.examDetails.duration % 60 > 0 && (
                        <div>{this.props.examDetails.duration % 60} mins</div>
                      )}
                    </span>
                  </div>
                </div>
                <div className="col-span-3">
                  <div className="flex flex-col px-2 pb-2">
                    <span className="text-gray-500 text-sm italic">
                      Exam description:
                    </span>
                    <span className="text-gray-700 font-bold px-2 py-2 rounded bg-white mt-2">
                      {this.props.examDetails.discription}
                    </span>
                  </div>
                </div>
                <div className="col-span-3">
                  <div className="flex flex-col px-2 pb-2">
                    <span className="text-gray-500 text-sm italic">
                      Terms and conditions / Rules:
                    </span>
                    <div
                      className="px-2 py-2 rounded bg-white mt-2"
                      dangerouslySetInnerHTML={{
                        __html: this.props.examDetails.termes,
                      }}
                    ></div>
                  </div>
                </div>
              </div>
            </div>
            <div className="bg-gray-100 rounded mx-2 mb-2 px-2 py-2">
              <div className="font-bold text-xl text-blue-600 mb-4 flex flex-row items-center justify-between gap-2 bg-white py-2 px-2 rounded">
                <div className="flex flex-row items-center gap-2">
                  <MdLocationOn className="text-4xl text-blue-600" />
                  Examination location
                </div>
              </div>
              <div className="grid md:grid-cols-3 gap-4 px-2">
                <div className="col-span-1">
                  <div className="flex flex-col border-b border-gray-300 pb-2">
                    <span className="text-gray-500 text-sm italic">
                      Province:
                    </span>
                    <span className="text-gray-700 font-bold">
                      {this.props.locationsData.Locations_list &&
                        this.props.locationsData.Locations_list.find(
                          (itm) =>
                            this.props.locationDetails !== undefined &&
                            itm.center_id ===
                              this.props.locationDetails.center_id
                        )?.province_name}
                    </span>
                  </div>
                </div>
                <div className="col-span-1">
                  <div className="flex flex-col border-b border-gray-300 pb-2">
                    <span className="text-gray-500 text-sm italic">
                      District:
                    </span>
                    <span className="text-gray-700 font-bold">
                      {this.props.locationsData.Locations_list &&
                        this.props.locationsData.Locations_list.find(
                          (itm) =>
                            this.props.locationDetails !== undefined &&
                            itm.center_id ===
                              this.props.locationDetails.center_id
                        )?.district_name}
                    </span>
                  </div>
                </div>
                <div className="col-span-1">
                  <div className="flex flex-col border-b border-gray-300 pb-2">
                    <span className="text-gray-500 text-sm italic">
                      Center:{" "}
                    </span>
                    <span className="text-gray-700 font-bold">
                      {this.props.locationDetails.center_name}
                    </span>
                  </div>
                </div>
                <div className="col-span-1">
                  <div className="flex flex-col border-b border-gray-300 pb-2">
                    <span className="text-gray-500 text-sm italic">
                      Room number:{" "}
                    </span>
                    <span className="text-gray-700 font-bold">
                      {this.props.locationDetails.room_number}
                    </span>
                  </div>
                </div>
                <div className="col-span-1">
                  <div className="flex flex-col border-b border-gray-300 pb-2">
                    <span className="text-gray-500 text-sm italic">
                      Proctoring type
                    </span>
                    <span className="text-gray-700 font-bold">
                      {this.props.locationDetails.proctoring_type}
                    </span>
                  </div>
                </div>
              </div>
            </div>
          </div>
        )}
      </div>
    );
  }
}

export default ExamDetails;
