import React, { Component } from "react";
import { HiInformationCircle } from "react-icons/hi";
import { IoMdInformationCircleOutline } from "react-icons/io";
import { MdCheckCircle, MdPauseCircleFilled } from "react-icons/md";
import { RiCheckboxBlankCircleFill, RiErrorWarningLine } from "react-icons/ri";
import { applicationPeriodsStoreInterface } from "../../../actions/application_periods";
import {
  candidateExamStoreInterface,
  InitialExamDetails,
} from "../../../actions/candidate_exam";
import { courseStoreInterface } from "../../../actions/course";
import { positionsStoreInterface } from "../../../actions/positions";
import Loading from "../../../components/Loading/Loading";
import { applicationExamAPI } from "../../../interfaces/application";
import { course } from "../../../interfaces/course";
import { UserExamStatus } from "../../../interfaces/exam";
import { PositionAPI } from "../../../interfaces/position";
import { DATE } from "../../../utils/functions";
import { ExamTimeStatus, validateExamDate } from "../../../utils/question_bank";

interface ExamsDoneProps {
  selectedPosition: PositionAPI;
  selectedExams: applicationExamAPI[] | null;
  findExam: (courseId: string) => InitialExamDetails | null;
  loadingExams: boolean;
  loadingExamsDone: boolean;
  setMaxExamModal: (status: boolean) => void;
  course: courseStoreInterface;
  setSelectedCourse: (course: course | null) => void;
  candidateExam: candidateExamStoreInterface;
  positions: positionsStoreInterface;
  periods: applicationPeriodsStoreInterface;
  setSelectedPosition: (position: PositionAPI) => void;
  setSelectedCandidateExam: (exam_id: string) => void;
}
interface ExamsDoneState {}

export class ExamsDone extends Component<ExamsDoneProps, ExamsDoneState> {
  render() {
    let ExamsDoneFilter = this.props.selectedPosition.courses.filter(
      (itm) =>
        this.props.findExam(itm.course_id) !== null &&
        this.props.candidateExam.user_exams_done.filter(
          (ex) =>
            ex.course === itm.course_id &&
            (ex.user_status === UserExamStatus.Done ||
              ex.user_status === UserExamStatus.Removed ||
              validateExamDate(
                ex.exam_date,
                ex.exam_start_time,
                ex.exam_end_time
              ) === ExamTimeStatus.Expired)
        ).length > 0
    );
    // console.log("Done ex: ", ExamsDoneFilter);
    return (
      <div className="bg-white px-2 py-2">
        <div className="bg-gray-100 rounded px-2 py-2">
          <div className="flex flex-row items-center justify-between text-xl mb-3 mx-2">
            <span className="font-bold ">Completed exams</span>
            <span className="font-bold bg-white text-gray-600 rounded-full px-2 text-base border border-gray-200">
              {this.props.selectedPosition !== null &&
                this.props.selectedExams !== null &&
                this.props.loadingExams === false &&
                ExamsDoneFilter.length}
            </span>
          </div>
          <div className="flex flex-col gap-2">
            {this.props.selectedPosition === null ||
            this.props.selectedExams === null ||
            this.props.loadingExams === true ? (
              <Loading />
            ) : ExamsDoneFilter.length === 0 ? (
              <div className="border border-gray-200 text-gray-400 font-bold rounded-md px-3 py-3 text-center">
                <div className="flex justify-center items-center w-full text-4xl">
                  <IoMdInformationCircleOutline />
                </div>
                <div className="text-gray-500">No exams found!</div>
              </div>
            ) : (
              ExamsDoneFilter.map((item, i) => (
                <div key={i + 1} className={`px-3 py-2 rounded group bg-white`}>
                  <div className="grid md:grid-cols-12 gap-2">
                    {/* First col */}
                    <div className="col-span-12 md:col-span-6 lg:col-span-4 flex fex-row items-center gap-3">
                      {this.props.candidateExam.user_exams_done.find(
                        (itm) => itm.course === item.course_id
                      ) !== undefined &&
                        this.props.candidateExam.user_exams_done.find(
                          (itm) => itm.course === item.course_id
                        )?.user_status === UserExamStatus.Editing && (
                          <div className="flex flex-row gap-2 items-center">
                            <div
                              className={`text-gray-300 flex flex-row items-center gap-2 text-right ml-2`}
                            >
                              <HiInformationCircle className="text-4xl" />
                            </div>
                          </div>
                        )}
                      {this.props.candidateExam.user_exams_done.find(
                        (itm) => itm.course === item.course_id
                      ) !== undefined &&
                        this.props.candidateExam.user_exams_done.find(
                          (itm) => itm.course === item.course_id
                        )?.user_status === UserExamStatus.Done && (
                          <div className="flex flex-row gap-2 items-center">
                            <div
                              className={`text-blue-600 flex flex-row items-center gap-2 text-right ml-2`}
                            >
                              <MdCheckCircle className="text-4xl" />
                            </div>
                          </div>
                        )}
                      {this.props.candidateExam.user_exams_done.find(
                        (itm) => itm.course === item.course_id
                      ) !== undefined &&
                        this.props.candidateExam.user_exams_done.find(
                          (itm) => itm.course === item.course_id
                        )?.user_status === UserExamStatus.Removed && (
                          <div className="flex flex-row gap-2 items-center">
                            <div
                              className={`text-gray-300 flex flex-row items-center gap-2 text-right ml-2`}
                            >
                              <MdCheckCircle className="text-4xl" />
                            </div>
                          </div>
                        )}
                      {this.props.candidateExam.user_exams_done.find(
                        (itm) => itm.course === item.course_id
                      ) !== undefined &&
                        this.props.candidateExam.user_exams_done.find(
                          (itm) => itm.course === item.course_id
                        )?.user_status === UserExamStatus.Paused && (
                          <div className="flex flex-row gap-2 items-center">
                            <div
                              className={`text-gray-300 flex flex-row items-center gap-2 text-right ml-2`}
                            >
                              <MdPauseCircleFilled className="text-4xl" />
                            </div>
                          </div>
                        )}
                      {this.props.candidateExam.user_exams_done.find(
                        (itm) => itm.course === item.course_id
                      ) === undefined &&
                        this.props.findExam(item.course_id) !== null &&
                        this.props.findExam(item.course_id)?.id !==
                          undefined && (
                          <div className="flex flex-row gap-2 items-center">
                            <div
                              className={`text-blue-600 flex flex-row items-center gap-2 text-right ml-2 animate-ping`}
                            >
                              <HiInformationCircle className="text-4xl" />
                            </div>
                          </div>
                        )}
                      {this.props.candidateExam.user_exams_done.find(
                        (itm) => itm.course === item.course_id
                      ) === undefined &&
                        (this.props.findExam(item.course_id) === null ||
                          this.props.findExam(item.course_id)?.id ===
                            undefined) && (
                          <div className="flex flex-row gap-2 items-center">
                            <div
                              className={`text-gray-300 group-hover:text-white flex flex-row items-center gap-2 text-right ml-2`}
                            >
                              <RiCheckboxBlankCircleFill className="text-4xl" />
                            </div>
                          </div>
                        )}
                      <div className="flex flex-col">
                        <div className={`font-bold text-gray-600 truncate`}>
                          {item.course_name}
                        </div>
                        <div className="flex flex-row justify-start items-center gap-2">
                          <span className="text-gray-500 text-xs">
                            Exam date:{" "}
                          </span>
                          <div className="flex flex-row items-center gap-2">
                            <span className="font-bold text-gray-800 text-sm">
                              {DATE(
                                this.props.findExam(item.course_id)!.exam_date,
                                "DD/MM/YYYY"
                              )}
                            </span>
                            <span
                              className={`bg-gray-100 text-gray-800 font-bold px-2 py-1 rounded-full text-sm`}
                            >
                              {this.props.findExam(item.course_id)!.start_time}
                            </span>
                            <span
                              className={`bg-gray-100 text-gray-800 font-bold px-2 py-1 rounded-full text-sm underline`}
                            >
                              {this.props.candidateExam.user_exams_done.find(
                                (x) => x.course === item.course_id
                              ) !== undefined
                                ? validateExamDate(
                                    this.props.candidateExam.user_exams_done.find(
                                      (x) => x.course === item.course_id
                                    )!.exam_date,
                                    this.props.candidateExam.user_exams_done.find(
                                      (x) => x.course === item.course_id
                                    )!.exam_start_time,
                                    this.props.candidateExam.user_exams_done.find(
                                      (x) => x.course === item.course_id
                                    )!.exam_end_time
                                  ) === ExamTimeStatus.Expired &&
                                  (this.props.candidateExam.user_exams_done.find(
                                    (x) => x.course === item.course_id
                                  )?.user_status === UserExamStatus.Editing
                                    ? "Not submitted"
                                    : this.props.candidateExam.user_exams_done.find(
                                        (x) => x.course === item.course_id
                                      )?.user_status === UserExamStatus.Paused)
                                  ? "Paused"
                                  : this.props.candidateExam.user_exams_done.find(
                                      (x) => x.course === item.course_id
                                    )?.user_status
                                : ""}
                            </span>
                          </div>
                        </div>
                      </div>
                    </div>
                    {/* Second col */}
                    <div className="col-span-12 md:col-span-6 lg:col-span-4 flex flex-col items-start justify-center">
                      {this.props.selectedExams === null ? (
                        ""
                      ) : this.props.selectedExams.find(
                          (itm) => itm.course_id === item.course_id
                        ) !== undefined ? (
                        <>
                          <div className="flex flex-row justify-start items-center gap-2">
                            <span className="text-gray-500 text-xs">
                              Proctoring type:{" "}
                            </span>
                            <span className="text-gray-700 font-bold text-sm">
                              {this.props.selectedExams === null
                                ? ""
                                : this.props.selectedExams.find(
                                    (itm) => itm.course_id === item.course_id
                                  ) !== undefined &&
                                  this.props.selectedExams.find(
                                    (itm) => itm.course_id === item.course_id
                                  )?.proctoring_type}
                            </span>
                          </div>
                          <div className="flex flex-col justify-center items-start gap-2">
                            {/* <span className="text-gray-500 text-xs">
                              Exam location:{" "}
                            </span> */}
                            <span className={`text-gray-700 font-bold text-sm`}>
                              Center:{" "}
                              {this.props.selectedExams === null ? (
                                ""
                              ) : this.props.selectedExams.find(
                                  (itm) => itm.course_id === item.course_id
                                ) !== undefined ? (
                                this.props.selectedExams.find(
                                  (itm) => itm.course_id === item.course_id
                                )?.center_name
                              ) : (
                                <span className="italic text-xs">
                                  No location selected
                                </span>
                              )}
                            </span>
                            <span className={`text-gray-700 font-bold text-sm`}>
                              Room:{" "}
                              {this.props.selectedExams === null ? (
                                ""
                              ) : this.props.selectedExams.find(
                                  (itm) => itm.course_id === item.course_id
                                ) !== undefined ? (
                                this.props.selectedExams.find(
                                  (itm) => itm.course_id === item.course_id
                                )?.room_number === 0 ? (
                                  <span className="bg-yellow-100 text-yellow-700 font-bold px-2 py-1 rounded">
                                    Waiting room, you are advised to choose
                                    another center
                                  </span>
                                ) : (
                                  <span className="text-blue-600 bg-blue-100 px-2 py-1 rounded">
                                    No:{" "}
                                    {
                                      this.props.selectedExams.find(
                                        (itm) =>
                                          itm.course_id === item.course_id
                                      )?.room_number
                                    }
                                  </span>
                                )
                              ) : (
                                <span className="italic text-xs">
                                  No location selected
                                </span>
                              )}
                            </span>
                          </div>
                        </>
                      ) : (
                        <div className="flex flex-row gap-2 items-center justify-start">
                          <RiErrorWarningLine className="text-xl text-yellow-700" />
                          <span className="text-yellow-700 italic text-sm">
                            No location selected
                          </span>
                        </div>
                      )}
                    </div>
                    {/* Third col */}
                    <div className="col-span-12 md:col-span-12 lg:col-span-4 items-center justify-start lg:justify-end">
                      {this.props.selectedExams === null ? (
                        ""
                      ) : this.props.selectedExams.find(
                          (itm) => itm.course_id === item.course_id
                        ) !== undefined ? (
                        <div className="flex flex-row justify-start md:justify-end items-center gap-2">
                          {this.props.loadingExams === true ||
                          this.props.loadingExamsDone === true ? (
                            <div className="text-gray-500 font-bold bg-gray-100 text-sm rounded px-3 py-2">
                              Checking...
                            </div>
                          ) : (
                            this.props.candidateExam.user_exams_done.find(
                              (itm) => itm.course === item.course_id
                            ) === undefined && (
                              <div
                                onClick={() => {
                                  this.setState({
                                    selectedCourse: item,
                                    centersModal: true,
                                  });
                                  this.props.setMaxExamModal(true);
                                }}
                                className="btn text-blue-600 border border-white cursor-pointer group-hover:bg-blue-600 hover:bg-blue-800 group-hover:text-white bg-blue-100 text-sm w-40 text-center"
                              >
                                Change location
                              </div>
                            )
                          )}
                        </div>
                      ) : (
                        <div className="flex flex-row justify-start md:justify-end items-center gap-2">
                          {this.props.candidateExam.user_exams_done.find(
                            (itm) => itm.course === item.course_id
                          ) === undefined && (
                            <div
                              onClick={() =>
                                this.setState({
                                  selectedCourse: item,
                                  centersModal: true,
                                })
                              }
                              className="btn cursor-pointer bg-yellow-700 hover:bg-yellow-50 border border-yellow-700 hover:text-yellow-700 text-white text-sm w-36 text-center"
                            >
                              Choose location
                            </div>
                          )}
                        </div>
                      )}
                      {this.props.candidateExam.user_exams_done.find(
                        (itm) => itm.course === item.course_id
                      ) !== undefined && (
                        // this.props.candidateExam.user_exams_done.find(
                        //   (itm) => itm.course === item.course_id
                        // )?.user_status !== UserExamStatus.Editing &&
                        <div className="flex flex-row gap-2 items-center justify-end h-full">
                          <div
                            className={`text-blue-600 flex flex-row items-center gap-2 text-right ml-2`}
                          >
                            <div className="flex flex-col gap-0">
                              <span className="text-sm">Marks</span>
                              <span className="text-lg font-bold -mt-1">
                                {this.props.candidateExam.user_exams_done
                                  .find((itm) => itm.course === item.course_id)
                                  ?.total_marks.toFixed(2)}
                                %
                              </span>
                            </div>
                          </div>
                        </div>
                      )}
                    </div>
                  </div>
                </div>
              ))
            )}
          </div>
        </div>
      </div>
    );
  }
}

export default ExamsDone;
